form
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 1em;
}
button
{
    margin-top: 1em;
    -webkit-align-self: flex-end;
            align-self: flex-end;
}
label
{
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    font-weight: 700;
    font-size: medium;
}
textarea
{
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}

form > div
{
    border-width: 0.1em;
    border-style: dashed;
    border-color: rgb(57, 57, 167);
    color: rgb(57, 57, 167);
    font-size: 1.15em;
}
section
{
    border-bottom: thick solid rgb(70, 70, 253);
}
